@import "../../_globalColor";

// Theme colors
$bg-dark: #121214;
$accent-yellow: #FFD700;
$text-white: #FFFFFF;
$text-gray: #9CA3AF;

.skills-main-div {
  display: flex;
  width: 100%;
  overflow: hidden;
  padding: 80px 5%;
  background: $bg-dark;
  gap: 40px;
  align-items: center;
}

.skills-text-div {
  flex: 1;
}

.skills-image-div {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.skills-image-div > img,
.skills-image-div > svg {
  max-width: 100%;
  height: auto;
  max-height: 400px;
}

.skills-heading {
  font-family: 'Plus Jakarta Sans', 'Montserrat', sans-serif;
  font-size: clamp(2rem, 4vw, 3.5rem);
  font-weight: 700;
  color: $text-white;
  margin-bottom: 0.5rem;
}

.subTitle {
  color: $accent-yellow !important;
  font-family: 'Inter', sans-serif;
  font-size: clamp(0.8rem, 1.2vw, 1rem);
  font-weight: 600;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  display: block;
}

.skills-text {
  font-family: 'Inter', sans-serif;
  font-size: clamp(0.9rem, 1.1vw, 1rem);
  color: $text-gray;
  line-height: 1.7;
  margin-bottom: 1rem;
}

.skills-text-subtitle {
  color: $text-gray;
  font-size: 1rem;
  margin-bottom: 1rem;
}

/* Media Query */
@media (max-width: 1380px) {
  .skills-main-div {
    padding: 60px 4%;
  }
}

@media (max-width: 1024px) {
  .skills-main-div {
    padding: 50px 4%;
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .skills-main-div {
    flex-direction: column;
    padding: 40px 5%;
    gap: 30px;
  }
  
  .skills-heading {
    text-align: center;
  }
  
  .skills-text-div {
    order: 1;
    text-align: center;
  }
  
  .skills-text {
    text-align: center;
  }
  
  .skills-text-subtitle {
    text-align: center;
  }
  
  .skills-image-div {
    order: 2;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  .skills-main-div {
    padding: 30px 4%;
  }
  
  .skills-image-div > img,
  .skills-image-div > svg {
    max-height: 250px;
  }
}

/* Light Mode Styles */
.light-mode {
  .skills-main-div {
    background: #FFFFFF;
  }

  .skills-heading {
    color: #1a1a2e;
  }

  .subTitle {
    color: #b8860b !important;
  }

  .skills-text {
    color: #555577;
  }

  .skills-text-subtitle {
    color: #555577;
  }
}

/* Scroll Animation Enhancements */
.skills-main-div {
  .skills-image-div,
  .skills-text-div {
    transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1),
                opacity 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  }
}

/* Skill items stagger animation */
.skills-text p {
  opacity: 0;
  transform: translateX(-20px);
  animation: skillFadeIn 0.5s ease forwards;
}

.skills-text p:nth-child(1) { animation-delay: 0.1s; }
.skills-text p:nth-child(2) { animation-delay: 0.2s; }
.skills-text p:nth-child(3) { animation-delay: 0.3s; }
.skills-text p:nth-child(4) { animation-delay: 0.4s; }
.skills-text p:nth-child(5) { animation-delay: 0.5s; }

@keyframes skillFadeIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Heading underline animation */
.skills-heading {
  position: relative;
  display: inline-block;
}

.skills-heading::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 0;
  height: 4px;
  background: linear-gradient(90deg, #FFD700, transparent);
  border-radius: 2px;
  animation: underlineGrow 1s ease 0.5s forwards;
}

@keyframes underlineGrow {
  to {
    width: 60%;
  }
}

.light-mode .skills-heading::after {
  background: linear-gradient(90deg, #b8860b, transparent);
}
